<script>
import axios from "axios";

export default {
  name: "vuethreelearn",
  props: {
    // msg: String
  },
  components: {
    // 挂载
    //CardInfoDetail,
  },
  data() {
    return {
      title: "生活管理",
      // 获取卡片信息
      cardinfo: {},
      // 左侧边栏数据
      data_aside: [],
    };
  },
  mounted() {
    this.get_cardinfo();
    this.get_data_aside();
  },
  methods: {
    get_cardinfo() {
      let __this = this;
      axios.get("/card/card.json").then(
        (response) => {
          console.log(response);
          __this.cardinfo = response["data"];
        },
        (error) => {
          console.log(error);
          alert(error);
        }
      );
    },
    get_data_aside() {
      let __this = this;
      axios.get("/aside/zhengxi.json").then(
        (response) => {
          var data_obj = response["data"];
          var data_str = JSON.stringify(data_obj);
          console.log(data_str);
          __this.data_aside = data_obj;
        },
        (error) => {
          console.log(error);
          alert(error);
        }
      );
    },
  },
};
</script>

